.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

#mapp{
  left: 200px;
}

#first {
  color: white;
  border: 1px solid white;
  background-color: black;
  width: 400px;
  height: 300px;
  z-index: 9999;
}

#second {
  color: white;
  margin-left: 200px;
  padding-left: 1px;
  margin-top: 100px;
  padding-top: 1px;
  border: 1px solid white;
  background-color: black;
  width: 600px;
  height: 600px;
  z-index: 9999;
}

#third {
  color: white;
  margin-left: 500px;
  padding-left: 1px;
  margin-top: 400px;
  padding-top: 1px;
  border: 1px solid white;
  background-color: black;
  width: 100px;
  height: 500px;
  z-index: 9999;
}

#img{
  background-image: url(ooCtg.jpg);
  position:absolute;
  width: 1500px;
  height: 1500px;
  z-index: 1;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
